* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

.rte p {
  font-size: 16px;
  color: #4D4D4D;
  letter-spacing: 0;
  margin-bottom: 20px;
}

.rte h4 {
  font-weight: bold;
  font-size: 16px;
  color: #4D4D4D;
  letter-spacing: 0;
  margin-top: 40px;
  margin-bottom: 20px;
}

.template-index .header {
  background: transparent;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  background: #F5EFE7;
}

.header__body {
  display: flex;
  justify-content: space-between;
  padding: 25px 30px;
  transition: .3s all !important;
  align-items: flex-start;
}

@media (max-width: 1023px) {
  .header__body {
    padding: 20px;
  }
}

.header.fixmenu {
  background: #fff;
}

.header.fixmenu .header__body {
  padding: 12px 30px;
  box-shadow: 0px 2px 20px #ddddddad;
  align-items: center;
}

.header.fixmenu .header__heading img {
  width: 90px;
}

@media (max-width: 767px) {
  .header.fixmenu .header__heading img {
    width: 60px;
  }
}

.header__heading {
  margin: 0;
}

.header__heading img {
  width: 140px;
  height: auto;
  transition: .3s all !important;
}

@media (max-width: 1023px) {
  .header__heading img {
    width: 98px;
  }
}

.header__right {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  position: relative;
}

.header__icon {
  margin-right: 24px;
  position: relative;
}

.header__icon img {
  width: 24px;
  height: auto;
}

.header__icon .cart-count-bubble {
  position: absolute;
  top: -5px;
  right: -10px;
  left: unset;
  bottom: unset;
  background: #ab8b8c;
  color: #fff;
  font-weight: 600;
  width: 20px;
  height: 20px;
  font-size: 10px;
}

.header__button {
  cursor: pointer;
  border: 0;
  background: transparent;
  height: 28px;
  position: relative;
  z-index: 10;
}

.header__button-line {
  margin-bottom: 10px;
  width: 45px;
  background: #A98989;
  height: 2px;
  display: block;
  transition: .3s all !important;
}

.header__button-line:last-child {
  margin-bottom: 0;
}

.header__button-line.active {
  background: #fff;
  transform-origin: 10% 40%;
}

.header__button-line.active:first-child {
  transform: rotate(20deg);
}

.header__button-line.active:last-child {
  transform: rotate(-20deg);
}

.header__menu-small li {
  margin-bottom: 15px;
}

.header__menu-small li:last-child {
  margin: 0;
}

.header__menu-small li a {
  font-size: 16px;
}

.header__inline-menu ul li {
  margin-bottom: 25px;
  text-decoration: none;
  font-weight: bold;
  font-size: 24px;
}

.header__inline-menu ul li:last-child {
  margin: 0;
}

.header__inline-menu ul li span {
  text-decoration: none;
  border: 0;
}

.menu-nav {
  background: #AB8B8C;
  position: fixed;
  top: 0;
  right: -500px;
  width: 446px;
  height: 100%;
  color: #fff;
  transition: .4s all !important;
  padding: 80px;
  box-sizing: border-box;
}

.menu-nav.active {
  right: 0;
}

.menu-nav .header__inline-menu {
  margin: 0 0 25px 0;
}

.menu-nav .header__submenu {
  padding-left: 20px;
  border-left: 1px solid #fff;
  margin-top: -5px;
}

.menu-nav .header__submenu li {
  margin-bottom: 20px;
}

.menu-nav .header__submenu li:last-child {
  margin-bottom: 0;
}

.menu-nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu-nav ul li:last-child {
  margin-bottom: 0;
}

.menu-nav a {
  color: #fff;
  text-decoration: none;
  font-size: 24px;
  font-weight: bold;
}

.menu-nav a span {
  text-decoration: none;
}

